import { createSlice } from '@reduxjs/toolkit';

// store.js       .

// 1. string state  .
const user1 = createSlice({
  // state 
  name: 'user1',
  // state 
  initialState: 'june',
  // state  
  //     state.
  reducers: {
    change(state) {
      return 'jujuny';
    },
    //     . 
  }
});
//   export
// export {1, 2, ...} = state.actions 
export const { change } = user1.actions;


// 2. object state  .
const user2 = createSlice({
  name: 'user2',
  initialState: { name: 'jo', age: 1 },
  reducers: {
    changeName(state) {
      // string return    .
      state.name = 'june';
    },
    changeAge(state) {
      state.age += 1;
    },
    //     state
    //          .
    increase(state, action) {
      // ex) increase(10) +10, increase(100) +100
      state.age += action.payload;
    }
  }
});
export const { changeName, changeAge, increase } = user2.actions;

export { user1, user2 };